@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

// styling the root

* {
    font-family: "Poppins", sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

// styling the navbar
.nav-header {
    position: fixed;
    width: 100%;
    display: flex;
    z-index: 999;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 150px;
    background-color: rgb(240, 240, 240);
}

.logo {
    text-transform: uppercase;
    text-decoration: none;
    color: rgb(0, 116, 116);
    font-size: 1.8em;
    font-weight: 600;
}

.nav-bar a {
    text-decoration: none;
    padding-left: 50px;
    color: rgb(0, 116, 116);
    font-weight: 500;
    transition: 1s ease;
}

.nav-bar a:hover {
    text-shadow: 1px 1px 1px black;
}

// styling the main section
section {
    padding: 100px 200px;
}

.main {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-image: linear-gradient(115deg, rgba(1, 91, 107, 0.8), rgba(1, 111, 131, 0.7)), url(images/pw3.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
}

.main h2 {
    color: white;
    font-size: 1.4em;
    font-weight: 500;
}

.main h2 span {
    display: inline-block;
    margin-top: 10px;
    color: rgb(189, 189, 0);
    font-size: 1.5em;
    font-weight: 600;
    letter-spacing: 4px;
}

.main h3 {
    margin-top: 10px;
    margin-bottom: 30px;
    color: white;
    font-size: 1.6em;
    font-weight: 600;
    letter-spacing: 1px;
}

.main-btn {
    text-decoration: none;
    font-size: 1.1em;
    font-weight: 500;
    letter-spacing: 2px;
    color: white;
    background-color: rgba(0, 0, 0, 0.5);
    display: inline-block;
    padding: 1em 2em;
    border-radius: 25px;
    margin-bottom: 25px;
    transition: 1s ease;
}

.main-btn:hover {
    background-color: rgba(189, 189, 0, 0.8);
    transform: scale(1.1);
    color: rgb(26, 26, 26);
}

.social-icons a {
    display: inline-block;
    color: white;
    padding-right: 30px;
    font-size: 1.7em;
    transition: 0.2s ease;
}

.social-icons a:hover {
    transform: scale(1.9);
}

// styling the skills section

.skills-section {
    background-color: rgb(240, 240, 240);
    padding: 60px 30px;
    margin-top: 30px;
}

.skills-container {
    display: flex;
    flex-direction: row;
    gap: 0.7em;
    justify-content: center;
    flex-wrap: wrap;
}

.skills-container h3 {
    background-color: rgb(0, 116, 116);
    padding: 10px;
    border-radius: 10px;
    color: white;
    letter-spacing: 1px;
    font-size: 1.1em;
}

// styling the services section
.title {
    display: flex;
    justify-content: center;
    color: rgb(0, 116, 116);
    font-size: 1.5em;
    font-weight: 700;
    margin-bottom: 30px;
}

.content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1.5em;
}

.card {
    background-color: white;
    width: 18em;
    box-shadow: 0 5px 25px rgba(1, 1, 1, 25%);
    border-radius: 25px;
    padding: 25px;
    transition: 1s;
}

.card:hover {
    transform: scale(1.1);
}

.icon {
    color: rgb(0, 116, 116);
    font-size: 4em;
    text-align: center;
}

.info {
    text-align: center;
}

.info h3 {
    color: rgb(0, 116, 116);
    font-size: 1.5em;
    font-weight: 500;
    letter-spacing: 2px;
    margin: 10px;
}

// styling the projects section

.projects {
    background-color: rgb(0, 27, 32);
}

.projects .content {
    margin-top: 30px;
}

.project-card {
    background-color: white;
    border: 1px solid white;
    min-height: 14em;
    width: 20em;
    overflow: hidden;
    border-radius: 10px;
    transition: 1s ease;
}

.project-card:hover {
    transform: scale(1.1);
}

.project-card:hover .project-image {
    opacity: 0.9;
}

.project-image {
    width: 100%;
}

.project-image img {
    width: 100%;
    max-height: 20%;
}

.project-info {
    padding: 1em;
}

.project-category {
    font-size: 0.8em;
}

.project-title {
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1px;
    margin-top: 10px;
}

.more-details {
    text-decoration: none;
    color: rgb(0, 116, 116);
}

.more-details:hover {
    text-shadow: 1px 1px 1px black;
}

// styling the contact section
.contact .info p {
    font-size: 1.2em;
    font-weight: 500;
}

// styling the footer section
.footer {
    background-color: rgb(0, 27, 32);
    display: flex;
    justify-content: space-between;
    color: white;
    padding: 1.6em;
}

.footer-title {
    font-size: 1.1em;
    font-weight: 400;
}

.footer-title span {
    color: rgb(0, 116, 116);
    letter-spacing: 1px;
}

// responsive web

@media (max-width: 720px) {
    .nav-header {
        padding: 12px 10px;
    }

    .nav-bar {
        padding-left: 0px;
    }

    .nav-bar a{
        padding-left: 10px;
        font-size: 0.9em;
    }
    .main h2{
        font-size: 1.2em;
    }
    .main h3 {
        font-size: 1.6em;
    }
    section {
        padding: 80px 20px;
    }
}


